import React, { Component } from 'react';

import { MediaQuery, withMediaQuery } from 'fluid-react';

import { AppStyle, CssDemoStyle } from '../theme';
import {
    Intro,
    H3,

    Row,
    Col
} from '../components';

import { codeSnippet } from './Common';

const Panel = (props) => (
    <div {...props} />
)

const ResponsiveColor = withMediaQuery(Panel);

const blankStyle = {
    backgroundColor: '#fff',
    borderColor: '#fff'
}

const contentStyle = {
    backgroundColor: '#5bc0de',
    borderColor: '#5bc0de',
    minHeight: '5rem',
    margin: '1px'
}

const Mobile = (props) => (
    <Row>
        <Col style={blankStyle}></Col>
        <Col xs="10" style={contentStyle}>
            No sidebar in Mobile version
        </Col>
        <Col style={blankStyle}></Col>
    </Row>
)

const Desktop = (props) => (
    <Row>
        <Col xs="9" style={contentStyle}>
            Desktop version
        </Col>
        <Col style={contentStyle}>
            Sidebar
        </Col>
    </Row>
)

class CssDemo extends Component {
    constructor(props) {
        super(props);

        this.onBreakpoint = this.onBreakpoint.bind(this);

        this.state = {
            version: 'mobile'
        }
    }

    componentDidMount() {
        MediaQuery.listenBreakpoint(this.onBreakpoint);
    }

    componentWillUnmount() {
        MediaQuery.unlistenBreakpoint(this.onBreakpoint);
    }

    onBreakpoint(vw) {
        this.setState({
            version: ['xs', 'sm'].includes(vw)? 'mobile' : 'desktop'
        });
    }

    render() {
        return (
            <div style={AppStyle.content}>
                <Intro>CSS Demo</Intro>

                <H3>Responsive Style</H3>
                <ResponsiveColor style={CssDemoStyle.responsiveColor}>
                    Color changes with width
                </ResponsiveColor>
                {codeSnippet(`
const CssDemoStyle = {
    responsiveColor: {
        margin: 'auto 1rem',
        padding: '2rem',
        color: '#fff',
        backgroundColor: '#0275d8',
        borderColor: '#0275d8',
        '@media (min-width: 576px) and (max-width: 767px)': {
            backgroundColor: '#5cb85c',
            borderColor: '#5cb85c'
        },
        '@media (min-width: 768px) and (max-width: 991px)': {
            backgroundColor: '#5bc0de',
            borderColor: '#5bc0de'
        },
        '@media (min-width: 992px) and (max-width: 1199px)': {
            backgroundColor: '#f0ad4e',
            borderColor: '#f0ad4e'
        },
        '@media (min-width: 1200px)': {
            backgroundColor: '#d9534f',
            borderColor: '#d9534f'
        }
    }
}

const Panel = (props) => (
    <div {...props} />
)

const ResponsiveColor = withMediaQuery(Panel);

export default class Comp extends Component {
    render() {
        <ResponsiveColor style={CssDemoStyle.responsiveColor}>
            Color changes with width
        </ResponsiveColor>
    }
}
                `)}

                <H3>Breakpoint</H3>
                {this.state.version === 'mobile'? <Mobile /> : <Desktop />}
                {codeSnippet(`
export default class Comp extends Component {
    constructor(props) {
        super(props);

        this.onBreakpoint = this.onBreakpoint.bind(this);

        this.state = {
            version: 'mobile'
        }
    }

    componentDidMount() {
        MediaQuery.listenBreakpoint(this.onBreakpoint);
    }

    componentWillUnmount() {
        MediaQuery.unlistenBreakpoint(this.onBreakpoint);
    }

    onBreakpoint(vw) {
        this.setState({
            version: ['xs', 'sm'].includes(vw)? 'mobile' : 'desktop'
        });
    }

    render() {
        {this.state.version === 'mobile'? <Mobile /> : <Desktop />}
    }
}
                `)}
            </div>
        )
    }
}

export default CssDemo;



